import React from 'react'
import { useState } from 'react/cjs/react.development'
const Contact = () => {
    const [data, setData] = useState({
        email: "",
        pass: "",
    });

    const inputEvent = (event) => {
        const { name, value } = event.target;
        setData((preval) => {
            return {
                ... preval,
                [name]: value,
            }

        })
    };
    const formSubmit = (e) => {
        e.preventDefault();
        alert(`${data.email} ${data.pass} `);

    }
    return (
        <>
            <div className="container mt-5 fo">      <form onSubmit={formSubmit}>
                <div class="mb-3">
                    <label for="exampleInputEmail1" class="form-label">Email address</label>
                    <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" value={data.email} name="email" onChange={inputEvent} />
                    <div id="emailHelp" class="form-text">We'll never share your email with anyone else.</div>
                </div>
                <div class="mb-3">
                    <label for="exampleInputPassword1" class="form-label">Password</label>
                    <input type="password" class="form-control" id="exampleInputPassword1" value={data.pass} name="pass" onChange={inputEvent} />
                </div>

                <button class="btn btn-primary">Submit</button>
            </form> </div> </>
    )
}
export default Contact;