import React from 'react'
import {Link} from 'react-router-dom';
const Common = (props) => {
    return (
        <>
            <div id="header" className="mt-5 p-5 d-flex align-items-center">
                <div className="container-fluid nav_bg">
                    <div className="row mx-auto">

                        <div className="row">

                            <div className="col-md-6 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                                <h1>Grow Your Business with <strong>Pawan Kumar</strong></h1>
                                <h2 className="my-3">
                                  {props.subheading}

                                </h2>
                                <div className="mt-3">
                                    <Link to={props.target} className="btn btn-primary">  {props.btn}</Link>
                                </div>
                            </div>
                            <div className="col-lg-6 order-1 order-lg-2 header-img d-flex justify-content-center flex-column">
                                <img src={props.img} alt="img" className="HomeImg" />

                            </div>
                        </div>
                    </div>

                </div>

            </div>
        </>
    )
}
export default Common;