import React from 'react'

import {
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink
} from "react-router-dom";


const Nav = () => {

  const me = () => {
    var x = document.getElementById("me");
    var y = document.getElementById("li");


    x.addEventListener("click", function () {
      if (y.style.display === "block") {
        y.style.display = "none";
      }
      else {
        y.style.display = "block";
      }
    })

  }
  return (
    <>




      <header className="header-area header-sticky wow slideInDown" data-wow-duration="0.75s" data-wow-delay="0s">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <nav className="main-nav">
                <NavLink to="./" className="logo" activeClassName="active" >
                  <h4>Pawan<span>Kumar</span></h4>
                </NavLink>

                <ul className="nav" id="li">
                  <li className="scroll-to-section"><NavLink to="/Home" activeClassName="active">Home</NavLink></li>
                  <li className="scroll-to-section"><NavLink to="/About" activeClassName="active">About Us</NavLink></li>
                  <li className="scroll-to-section"><NavLink to="/Service" activeClassName="active">Services</NavLink></li>

                  <li className="scroll-to-section"><div className="main-red-button"><NavLink to="/Contact" activeClassName="active">Contact Now</NavLink></div></li>
                </ul>
                <a className='menu-trigger' id="me" onClick={me} >
                  <span>Menu</span>
                </a>
              </nav>
            </div>
          </div>
        </div>
      </header>


    </>
  )
}
export default Nav;